'use client'

import React, { useEffect, useState } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import styles from './login.module.css'
import { useRouter } from 'next/navigation'
import { z } from 'zod'

// Ícones e fotos
import foto_login from './../../images/fotos/ilustracao_login.png'
import seta_esquerda_preta from './../../images/icones/seta_esquerda_preta.svg'
import google from './../../images/icones/google.svg'
import facebook from './../../images/icones/facebook.svg'
import envelope from './../../images/icones/envelope.svg'
import cadeado from './../../images/icones/cadeado.svg'

// Schema de validação com Zod
const loginSchema = z.object({
    email: z.string().email('E-mail inválido'),
    password: z.string().min(1, 'Senha é obrigatória')
})

export default function Login() {
    const router = useRouter()
    const [isExternalReferrer, setIsExternalReferrer] = useState(false)
    const [formData, setFormData] = useState({
        email: '',
        password: ''
    })
    const [error, setError] = useState<string | null>(null)
    const [isLoading, setIsLoading] = useState(false)

    useEffect(() => {
        if (!document.referrer || !document.referrer.includes(window.location.origin)) {
            setIsExternalReferrer(true)
        }
    }, [])

    const handleBack = () => {
        if (isExternalReferrer) {
            router.push('/')
        } else {
            router.back()
        }
    }

    const handleLogin = async (e: React.FormEvent) => {
        e.preventDefault()
        setIsLoading(true)
        setError(null)

        // Validação com Zod
        const result = loginSchema.safeParse(formData)
        if (!result.success) {
            setError(result.error.errors[0].message)
            setIsLoading(false)
            return
        }

        try {
            const response = await fetch('http://localhost/dashboard/api-designodyssey//auth/login_usuarios.php', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    email: formData.email,
                    senha: formData.password
                })
            })

            const data = await response.json()

            if (!response.ok || !data.success) {
                throw new Error(data.message || 'Erro ao fazer login')
            }

            // Redireciona conforme resposta da API
            router.push(data.redirect || '/perfil')
            
        } catch (err) {
            console.error('Erro no login:', err)
            setError(err instanceof Error ? err.message : 'Credenciais inválidas')
        } finally {
            setIsLoading(false)
        }
    }

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target
        setFormData(prev => ({
            ...prev,
            [name]: value
        }))
    }

    return (
        <div className={styles.login_main}>
            <div className={styles.login_content}>
                <div className={styles.login_content_left}>
                    <Image 
                        src={foto_login} 
                        height={500} 
                        width={500} 
                        alt='Ilustração de login'
                        priority
                    />
                    <ul className={styles.login_left_list}>
                        <li className='mb-5'>Trabalho de qualidade feito mais rápido</li>
                        <li>Acesso a talentos e empresas em todo o mundo</li>
                    </ul>
                </div>
                
                <div className={styles.login_content_right}>
                    <div className={styles.login_right_top}>
                        <div 
                            className={styles.login_right_voltar} 
                            onClick={handleBack} 
                            style={{ cursor: 'pointer' }}
                        >
                            <Image 
                                src={seta_esquerda_preta} 
                                height={30} 
                                width={30} 
                                alt='Voltar'
                            />
                            Voltar
                        </div>
                        
                        <div className={styles.login_right_bemvindo}>
                            <h1>Bem-vindo de volta!</h1>
                            <p>Faça login para acessar sua conta</p>
                        </div>

                        <div className={styles.social_login_options}>
                            <button 
                                type="button" 
                                className={styles.social_button}
                            >
                                <Image src={google} height={20} width={20} alt='Google' />
                                Entrar com Google
                            </button>
                            <button 
                                type="button" 
                                className={styles.social_button}
                            >
                                <Image src={facebook} height={20} width={20} alt='Facebook' />
                                Entrar com Facebook
                            </button>
                        </div>

                        <div className={styles.divider}>
                            <span>ou</span>
                        </div>

                        <form 
                            className={styles.login_right_bottom} 
                            onSubmit={handleLogin}
                        >
                            <div className={styles.login_credenciais}>
                                <div className={styles.login_field}>
                                    <label htmlFor="email">E-mail</label>
                                    <div className={styles.input_container}>
                                        <input
                                            id="email"
                                            name="email"
                                            type="email"
                                            className={styles.login_input}
                                            placeholder='seu@email.com'
                                            value={formData.email}
                                            onChange={handleChange}
                                            disabled={isLoading}
                                            required
                                        />
                                        <Image
                                            src={envelope}
                                            height={20}
                                            width={20}
                                            alt='Ícone de e-mail'
                                            className={styles.input_icon}
                                        />
                                    </div>
                                </div>

                                <div className={styles.login_field}>
                                    <label htmlFor="password">Senha</label>
                                    <div className={styles.input_container}>
                                        <input
                                            id="password"
                                            name="password"
                                            type="password"
                                            className={styles.login_input}
                                            placeholder='••••••••'
                                            value={formData.password}
                                            onChange={handleChange}
                                            disabled={isLoading}
                                            required
                                        />
                                        <Image
                                            src={cadeado}
                                            height={20}
                                            width={20}
                                            alt='Ícone de cadeado'
                                            className={styles.input_icon}
                                        />
                                    </div>
                                </div>

                                {error && (
                                    <div className={styles.login_error}>
                                        {error}
                                    </div>
                                )}

                                <div className={styles.login_actions}>
                                    <button 
                                        type="submit" 
                                        className={styles.login_button}
                                        disabled={isLoading}
                                    >
                                        {isLoading ? (
                                            <span>Entrando...</span>
                                        ) : (
                                            <span>Entrar</span>
                                        )}
                                    </button>

                                    <div className={styles.login_links}>
                                        <Link 
                                            href="/recuperar-senha" 
                                            className={styles.forgot_password}
                                        >
                                            Esqueceu a senha?
                                        </Link>
                                        <span className={styles.register_text}>
                                            Ainda não tem uma conta?{' '}
                                            <Link 
                                                href="/registro" 
                                                className={styles.register_link}
                                            >
                                                Cadastre-se
                                            </Link>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}